/* GLOBAL AND APP CONTAINER */
* {
  box-sizing: border-box;
}

.app-container {
  display: block; 
  width: 100%;
  min-height: 100vh;
  position: relative;
  background-color: #050505;
  background-image: 
    linear-gradient(rgba(0, 243, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 243, 255, 0.03) 1px, transparent 1px);
  background-size: 40px 40px;
  overflow-x: hidden;
}

/* FIRST SECTION */
.hero-split-layout {
  display: grid;
  grid-template-columns: 1fr 1fr; 
  gap: 4rem;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  align-items: center; 
  
 
  min-height: 100vh; 
  padding: 0 5%; 
}

.hero-content {
  max-width: 100%; 
  position: relative;
  z-index: 2;
}

.hero-right {
  display: flex;
  justify-content: center;
  z-index: 2;
}

/* Typography */
.hero-title {
  font-family: 'Inter', sans-serif;
  font-size: 5rem;
  font-weight: 800;
  color: #e0e0e0;
  margin: 0;
  line-height: 1;
  position: relative;
  letter-spacing: -2px;
}

.accent-cyan { color: #00f3ff; }

.hero-subtitle {
  font-family: 'Fira Code', monospace;
  font-size: 1.5rem;
  color: #888;
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.system-text {
  font-family: 'Fira Code', monospace;
  color: #39ff14; 
  font-size: 1rem;
  margin-bottom: 1rem;
  letter-spacing: 1px;
}

.hero-description p {
  color: #bbb;
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 600px;
}

.cursor-blink {
  animation: blink 1s step-end infinite;
}

@keyframes blink { 50% { opacity: 0; } }

/* TERMINAL */
.terminal-window {
  width: 100%;
  max-width: 500px;
  background: rgba(15, 15, 15, 0.9);
  border: 1px solid #333;
  border-radius: 8px;
  box-shadow: 0 0 30px rgba(0, 243, 255, 0.1); 
  overflow: hidden;
  position: relative;
  backdrop-filter: blur(5px);
}

.terminal-header {
  background: #1a1a1a;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #333;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 8px;
}
.red { background: #ff5f56; }
.yellow { background: #ffbd2e; }
.green { background: #27c93f; }

.terminal-title {
  color: #666;
  font-family: 'Fira Code', monospace;
  font-size: 0.8rem;
  margin-left: auto; 
  margin-right: auto;
}

.terminal-body {
  padding: 20px;
  color: #a9b7c6; 
  text-align: left;
  min-height: 250px;
}

/* EFFECTS */
.pcb-trace {
  border-left: 2px solid #00f3ff;
  padding-left: 1.5rem;
  margin-bottom: 3rem;
  position: relative;
}

.pcb-trace::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -5px;
  width: 8px;
  height: 8px;
  background: #00f3ff;
  border-radius: 50%;
  box-shadow: 0 0 10px #00f3ff;
}

.glitch-layer, .glitch-layer-2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  display: none;
}

.hero-title:hover .glitch-layer {
  display: block;
  color: #00f3ff;
  animation: glitch-anim-1 1s infinite linear alternate-reverse;
}
.hero-title:hover .glitch-layer-2 {
  display: block;
  color: #ff0055;
  animation: glitch-anim-2 1s infinite linear alternate-reverse;
}

@keyframes glitch-anim-1 {
  0% { clip-path: inset(20% 0 80% 0); transform: translate(-2px, 0); }
  100% { clip-path: inset(60% 0 10% 0); transform: translate(2px, 0); }
}
@keyframes glitch-anim-2 {
  0% { clip-path: inset(10% 0 60% 0); transform: translate(2px, 0); }
  100% { clip-path: inset(80% 0 5% 0); transform: translate(-2px, 0); }
}

.scanline {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0) 50%,
    rgba(0, 0, 0, 0.1) 50%,
    rgba(0, 0, 0, 0.1)
  );
  background-size: 100% 4px;
  pointer-events: none; 
  z-index: 10;
  animation: scrollScanline 60s linear infinite;
}

@keyframes scrollScanline {
  0% { background-position: 0 0; }
  100% { background-position: 0 100vh; }
}

/* BUTTONS */
.button-group {
  display: flex;
  gap: 1.5rem;
}

.btn {
  font-family: 'Fira Code', monospace;
  font-size: 0.9rem;
  padding: 1rem 2rem;
  text-decoration: none;
  border: 1px solid;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background: transparent;
}
.btn-primary {
  border-color: #00f3ff;
  color: #00f3ff;
}

.btn-primary:hover {
  background-color: rgba(0, 243, 255, 0.1);
  box-shadow: 0 0 20px rgba(0, 243, 255, 0.4);
  color: white;
  text-shadow: 0 0 5px #00f3ff;
}

.btn-secondary {
  border-color: #555;
  color: #888;
}

.btn-secondary:hover {
  border-color: #fff;
  color: #fff;
}

/* PROJECTS */
.projects-section {
  padding: 100px 5%;
  min-height: 100vh;
  max-width: 1400px;
  margin: 0 auto;
}

.section-title {
  font-family: 'Fira Code', monospace;
  font-size: 2rem;
  color: #00f3ff;
  margin-bottom: 3rem;
  border-bottom: 1px solid #333;
  padding-bottom: 10px;
  display: inline-block;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.project-card {
  background: rgba(10, 10, 10, 0.8);
  border: 1px solid #333;
  padding: 2rem;
  position: relative;
  transition: all 0.3s ease;
  overflow: hidden;
}

.project-card:hover {
  border-color: #00f3ff;
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 243, 255, 0.1);
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: #00f3ff;
  transition: width 0.3s ease;
}

.project-card:hover::before {
  width: 100%;
}

.card-header {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.card-title {
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
  color: #e0e0e0;
  margin-left: 10px;
}

.card-category {
  font-family: 'Fira Code', monospace;
  font-size: 0.8rem;
  color: #555;
  margin-bottom: 1rem;
}

.card-desc {
  color: #aaa;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 2rem;
}

.tech-tag {
  font-family: 'Fira Code', monospace;
  font-size: 0.75rem;
  background: #1a1a1a;
  color: #39ff14; 
  padding: 5px 10px;
  border: 1px solid #333;
}

.card-actions {
  display: flex;
  gap: 20px;
}

.action-link {
  font-family: 'Fira Code', monospace;
  font-size: 0.9rem;
  color: #00f3ff;
  text-decoration: none;
  transition: color 0.2s;
}

.action-link:hover {
  color: #fff;
  text-shadow: 0 0 5px #00f3ff;
}

/* SKILLS SECTION */
.skills-section {
  padding: 50px 5%;
  max-width: 1400px;
  margin: 0 auto;
  margin-bottom: 50px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}


.skill-category-card {
  background: rgba(10, 10, 10, 0.6);
  border: 1px solid #222;
  padding: 1.5rem;
  position: relative;
  transition: all 0.3s ease;
}

.skill-category-card:hover {
  border-color: #555;
  background: rgba(15, 15, 15, 0.9);
}


.skill-category-card::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid #39ff14;
  border-right: 2px solid #39ff14;
  opacity: 0.5;
}

.category-title {
  font-family: 'Fira Code', monospace;
  font-size: 0.9rem;
  color: #888;
  margin-bottom: 1.5rem;
  border-bottom: 1px dashed #333;
  padding-bottom: 0.5rem;
}


.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}


.skill-chip {
  font-family: 'Inter', sans-serif;
  font-size: 0.85rem;
  font-weight: 600;
  background: #111;
  color: #e0e0e0;
  padding: 8px 16px;
  border: 1px solid #333;
  border-radius: 2px; 
  cursor: default;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}


.skill-chip:hover {
  border-color: #00f3ff;
  color: #fff;
  box-shadow: 0 0 8px rgba(0, 243, 255, 0.4);
  transform: translateY(-2px);
  background: #050505;
}

/* SYSTEM TIMER / PROGRESS BAR  */
.timer-container {
  width: 100%;
  max-width: 300px;
  margin: 20px auto; 
  font-family: 'Fira Code', monospace;
  text-align: left;
}

.timer-label {
  color: #888;
  font-size: 0.8rem;
  margin-bottom: 5px;
}

.progress-track {
  width: 100%;
  height: 4px;
  background: #111;
  border: 1px solid #333;
  position: relative;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #00f3ff; 
  width: 0%;
  animation: loadBar 7.5s linear forwards;
  box-shadow: 0 0 10px #00f3ff;
}


.skills-timer .progress-fill {
  background: #39ff14; 
  box-shadow: 0 0 10px #39ff14;
}

@keyframes loadBar {
  0% { width: 0%; }
  100% { width: 100%; }
}

.blinking-dots {
  animation: blink 1s infinite;
}

/* --- CONTACT SECTION --- */
.contact-section {
  padding: 100px 5%;
  max-width: 800px; 
  margin: 0 auto;
  margin-bottom: 100px;
  scroll-margin-top: 50px; 
}

.contact-container {
  background: rgba(10, 10, 10, 0.8);
  border: 1px solid #333;
  padding: 2px; 
}

.terminal-header-strip {
  background: #1a1a1a;
  padding: 10px 20px;
  font-family: 'Fira Code', monospace;
  font-size: 0.8rem;
  color: #555;
  border-bottom: 1px solid #333;
  display: flex;
  align-items: center;
  gap: 10px;
}

.status-light {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  box-shadow: 0 0 5px currentColor;
}
.status-light.green { background-color: #39ff14; color: #39ff14; }

.contact-prompt {
  color: #888;
  font-family: 'Fira Code', monospace;
  margin: 2rem;
  border-left: 2px solid #39ff14;
  padding-left: 1rem;
  font-size: 0.9rem;
}

.terminal-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 0 2rem 2rem 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

label {
  font-family: 'Fira Code', monospace;
  color: #00f3ff;
  font-size: 0.9rem;
}


input, textarea {
  background: #050505;
  border: none;
  border-bottom: 1px solid #333;
  color: #e0e0e0;
  font-family: 'Inter', sans-serif;
  padding: 15px;
  font-size: 1rem;
  transition: all 0.3s ease;
  width: 100%;
}


input:focus, textarea:focus {
  outline: none;
  border-bottom-color: #39ff14; 
  background: #0a0a0a;
  box-shadow: 0 2px 10px rgba(57, 255, 20, 0.1);
}

::placeholder {
  color: #444;
  font-style: italic;
  font-family: 'Fira Code', monospace;
}

.submit-btn {
  align-self: flex-start;
  margin-top: 1rem;
  width: 100%;
  text-align: center;
}

/* MEDIA QUERIES */


@media (max-width: 1024px) {
  .hero-split-layout {
    grid-template-columns: 1fr; 
    text-align: left;
    gap: 3rem;
    height: auto; 
    min-height: auto;
    padding-top: 100px; 
    padding-bottom: 100px;
  }
  
  .hero-right {
    justify-content: flex-start;
    width: 100%;
  }
  
  .terminal-window {
    max-width: 100%; 
  }

  .hero-title {
    font-size: 3rem; 
  }
  
  .button-group {
    flex-direction: column;
  }
  
  .btn {
    text-align: center;
  }
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
}